@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200..900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200..900&family=Montserrat:ital,wght@0,100..900;1,100..900&family=SUSE:wght@100..800&display=swap');

@font-face {
  font-family: orangee;
  src: url("./orange\ juice\ 2.0.ttf");
}

.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  /* width: 100vw; */
  /* height: 100vh; */
  background-color: lightblue;
  width: 100%;
}

.imgDiv{
  /* display: inline-block; */
  width: 100%;
}

.imgDiv img{
  width: 100%;
  background-color: lightblue;
}

.formsDiv{
  width: 400px;
  background-color: white;
}

.formComponentsDiv{
  width: 100%;
  display: flex;
  justify-content: center;
}

.formsDiv h1{
  height: 30px;
  color: lightseagreen;
  font-family: "Inconsolata", monospace;
}

