  margin: 20px;
  background-color: rgb(228, 245, 251);
} */

/* .formsDiv label{
  width: 90%;
  text-align: justify;
  margin: 20px;
} */

.inputsDiv input{
  width: 90%;
  height: 40px;
  margin: 10px;
  background-color: rgb(228, 245, 251);
}

select, option{
  width: 90%;
  height: 40px;
  margin: 10px;
  background-color: rgb(228, 245, 251);
}

legend{
  text-align: justify;
  font-family: "SUSE", sans-serif;
  text-shadow: 10px 10px 20px black;
  margin: 20px;
}

.formButtonsDiv{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.radioButtonsDiv{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 10px;
}

.radioButtonsDiv label{
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

span{
  text-align: left;
  color: red;
}

/* .norDiv{
  text-align: center;
} */

.labelHeading{
  border: 2px inset black;
  background-color: rgb(228, 245, 251);
}

.brnLink{
  background: lightblue;
  padding: 10px;
  width: 100%;
}

.pageNotFoundDiv{
  background-image: url("./images/bg.jpg"), url("./images/brn-logo.png");
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  background-position: center;
  background-attachment: fixed;
  background-size:cover, contain;
  font-size: 2rem;
  font-family: orangee;
}

